:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1280px;
  flex-wrap: wrap;
  gap: 16px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 32px;
  padding-right: 32px;
  display: flex;
}

.is-active {
  pointer-events: none;
  background-color: #ff6b01;
}

img {
  max-width: 100%;
  height: 100%;
  width: 100%;
  display: block;
}

.film-card {
  max-width: 100%;
  flex-direction: column;
  flex-basis: 32%;
  display: flex;
}

.info-item {
  font-size: 18px;
}

.btn-wrap {
  justify-content: center;
  display: flex;
}

